import React from 'react'

const Notification = ({ message, isPositive }) => {
    if (message === null) {
        return null
    }
    else if (isPositive === true) {
        return (
            <div className="success">
                {message}
            </div>
        )
    }
    else {
        return (
            <div className="error">
                {message}
            </div>
        )
    }
}
export default Notification